<template>
        <el-container class="home-container">
            <!-- 头部区 -->
        <el-header>
            <div>
                <img src="../assets/heima.png" alt="">
                <span>电商后台管理系统</span>
            </div>
        <el-button type="info" @click="hometoken">退出</el-button>        
        </el-header>
        <!-- 页面主体区 -->
        <el-container>
            <!-- 侧边栏 -->
            <el-aside :width="isCollapse ?'64px':'200px' ">
                <div class="toggle-button" @click="toggleCollapse">|||| </div>
                <!-- 侧边栏菜单区 -->
                 <el-menu
                    background-color="#333744"
                    text-color="#fff"
                    active-text-color="#409EFF"
                    unique-opened
                    :collapse="isCollapse"
                    :collapse-transition="false"
                    :router="true"
                    :default-active="activePath"
                    >
                    <!-- 一级菜单 -->
                    <el-submenu :index="item.id +''" v-for="item in menulist" :key="item.id">
                        <!-- 一级菜单的模板区域 -->
                        <template slot="title">
                            <!-- 图片 -->
                        <i :class="iconsObj[item.id]"></i>
                        <!-- 文本 -->
                        <span>{{item.authName}}</span>
                        </template>
                        <!-- 二级菜单 -->
                        <el-menu-item 
                            :index="'/'+subItem.path "
                            v-for="subItem in item.children"
                            :key="subItem.id"
                           @click="saveNavState('/'+subItem.path)"
                           >
                                <template slot="title">
                                 <!-- 图片 -->
                                <i class="el-icon-menu"></i>
                                    <!-- 文本 -->
                                <span>{{subItem.authName}}</span>
                                </template>
                        </el-menu-item>
                    </el-submenu>
                    </el-menu>
            </el-aside>

            <!-- 右侧内容主体 -->
            <el-main>
                <router-view></router-view>
            </el-main>
        </el-container>
</el-container>
       
</template>

<script>
export default {
    created(){
        this.getMenuList()
        this.activePath=window.sessionStorage.getItem("activePath")
    },
  data() {
    return {
      //str:0
    //   左侧菜单数据
      menulist:[],
      iconsObj:{
          '125':'iconfont icon-user',
          '103':'iconfont icon-tijikongjian',
          '101':'iconfont icon-shangpin',
          "102":'iconfont icon-danju',
          "145":'iconfont icon-baobiao'
      },
    //   是否折叠
        isCollapse:false,
    //   被激活的链接地址
        activePath:''
    };
  },
  methods: {
    hometoken() {
      window.sessionStorage.clear();
      this.$router.push("/login");
    },
    async getMenuList(){
        const {data:res} = await this.$http.get("menus")
        if(res.meta.status !==200)return this.$message.error(res.meta.msg)
        this.menulist=res.data
        
        console.log(this.menulist)
    },
    // 点击按钮切换菜单的折叠和张开
  toggleCollapse(){
      this.isCollapse=!this.isCollapse
  },
//   保存链接的激活状态
  saveNavState(activePath){
      window.sessionStorage.setItem("activePath",activePath)
      this.activePath= activePath
  }
    //数据更新
    //add(){
    //this.str++
    //},
    //数据销毁
    //de(){
    // this.destroy()
    //}
  }
  //组件创建之前调用
  //beforeCreate() {
  //console.log('vue...............beforeCreate---组件创建之前调用')
  //},
  //组件创建加载完成
  //created() {
  //console.log('vue...............created---组件创建加载完成')
  //},
  //数据挂在之前(渲染)
  //beforeMount() {
  //console.log('vue...............beforeMount---数据挂在之前(渲染)')
  //},
  //数据挂在完成(渲染)
  //mounted() {
  //.log('vue...............mounted---数据挂在完成(渲染)')
  //},
  //数据更新之前
  //beforeUpdate() {
  //console.log('vue...............beforeUpdate---数据更新之前')
  //},
  //数据更新完成
  //updated() {
  //onsole.log('vue...............updated---数据更新完成')
  //},
  //数据销毁之前
  //beforeDestroy() {
  //console.log('vue...............beforeDestroy---数据销毁之前')
  //},
  //数据销毁完成
  //destroyed() {
  //console.log('vue...............destroyed---数据销毁完成')
  //},
  //watch:{
  //route(){
  //console.log('vue...............destroyed---监听属性')
  //}
  //}
};
</script>

<style lang="less" scoped>
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #ffff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}
.el-aside {
  background-color: #333744;
  .el-menu{
      border-right:none;
  }
}
.el-main {
  background-color: #eaedf1;
}
.home-container {
  height: 100%;
}
.iconfont{
    margin-right:10px;
}
.toggle-button{
    background-color:#4A5064;
    font-size:10px;
    line-height: 24px;
    color:#ffff;
    text-align: center;
    letter-spacing: 0.2em;
    cursor:pointer;
}
</style>
